body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, #root, .App {
  height: 100%;
  width: 100%;
  background-color: rgb(84,20,44);
  font-family: 'Lora', serif;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content
}

.navbar-brand {
  float: left;
  justify-content: left;
}

main {
  background: url(/lpr-website/website-front-banner.png);
  background-attachment: fixed;
  background-repeat: repeat-y;
  background-size: cover;
  height: max(-webkit-max-content, 100%);
  height: max(max-content, 100%);
}

.col {
  margin-top: 200px;
}

.main-content-wrapper {
  border-radius: 20px;
  padding-top: 30px;
  padding-bottom: 50px;
  margin: auto;
  max-width: 80%;
  position: relative;
}

.main-content-paragraph-wrapper {
  background-color: rgb(255, 255, 255, .8);
  border-radius: 5px;
  padding: 10px;
  margin: 20px auto;
  max-width: 80%;
  max-height: 80%;
  position: relative;
  color:rgba(0, 0, 0);
  text-align: left;
}

.centered-text-paragraph {
  text-align: center;
}

img.resize {
  height: auto;
}

iframe {
  width: 80%;
}

ul {
  list-style-position: inside;
}

#about-page-video {
  margin: auto;
  padding: auto;
  max-width: 50%;
  max-height: 80%;
}
.App > nav {
    padding-bottom: 0;
    padding-top: 0;
    background-color: rgb(84,20,44) !important;
    font-size:large;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index:99;
}

.navbar {
    color: lightgray !important;
}

.App > nav > div.container {
    margin: 0;
    max-width: 100%;
}


.App > nav > div > a {
    font-size: 30px;
}
